* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  background-color: #e3d7e1;
  border: 4px solid #525293;
  border-radius: 10px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.flatpickr-input {
  border: 2px solid #525293;
  border-radius: 4px;
  padding: 10px;
  font-size: medium;
}

.flatpickr-months {
  background-color: #c9bfc3;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  display: flex;
}

.value {
  justify-content: center;
  margin-bottom: 15px;
  font-size: 40px;
  font-weight: bold;
}

.label {
  color: #5757b8;
  text-transform: uppercase;
  font-size: 20px;
}

.button-shape {
  color: #000;
  border-color: #696969;
  border-radius: 4px;
  font-size: medium;
}

